import { Component, ContentChild, EventEmitter, Input, Output, TemplateRef } from '@angular/core';

import { TiBaseComponent } from '@cloud/tiny3';
/**
 * 
 *
 * <example-url>../tinyplus3demo/#/collapsebutton/collapsebutton-all</example-url>
 */
@Component({
  selector: 'tp-collapsebutton',
  templateUrl: './collapsebutton.html',
  styleUrls: ['./collapsebutton.less'],
  host: {
    '[class.tp-collapsebutton-uncollapsed]': '!collapsed'
  }
})
export class TpCollapsebuttonComponent extends TiBaseComponent {

  /**
   * true false
   */
  @Input() collapsed: boolean = true;
  /**
   * 10.0.4 
   */
  @Input() searchCount: number = 0;
  /**
   *  
   */
  @Output() readonly collapsedChange: EventEmitter<boolean> = new EventEmitter<boolean>();
  /**
   * @ignore
   * 
   */
  @ContentChild(TemplateRef) textTemplateRef: TemplateRef<any>;

  /**
   * @ignore
   * 
   */
  public onClickToggle($event: Event): void {
    this.collapsed = !this.collapsed;
    this.collapsedChange.emit(this.collapsed);
  }

}
