import {
    Component,
    ElementRef,
    EventEmitter,
    Input,
    Output,
    Renderer2,
    SimpleChanges,
    ViewChild
} from '@angular/core';
import { TiBaseComponent, TiLocale, Util } from '@cloud/tiny3';

import { CfUtil } from '../../cfutils/CfUtil';
export interface TpDiscountDetail {
    /**
     * 
     */
    name?: string;
    /**
     * 
     */
    resourceName?: string;
    /**
     * 
     */
    specification?: string;
    /**
     * 
     */
    size?: string;
    /**
     * 
     */
    type?: number;
    /**
     * 
     */
    rate?: number;
    /**
     * 
     */
    href?: string;
    /**
     * 
     */
    expireTime?: string;
    /**
     * 
     */
    priceNum?: number;
    /**
     * id
     *
     * 
     */
    id?: string;
    /**
     * 
     */
    isRecommend?: boolean;
    /**
     * 
     */
    [propName: string]: any;
}

export interface TpDiscoutsConfig {
    /**
     *  
     */
    type: 'period' | 'ondemand';
    /**
     * 
     * 1.2-4124
     * 
     */
    numFormat?: string;
    /**
     * 
     */
    selectedDiscounts?: Array<TpDiscountDetail>;
    /**
     * ID
     */
    selectedId?: string;
    /**
     * 
     */
    selectableDiscounts?: Array<TpDiscountDetail>;
    /**
     * 
     */
    discounts?: Array<TpDiscountDetail>;
    /**
     * 
     */
    maxHeight?: number;
    /**
     * 
     *  10.1.0 
     */
    measureUnit?: string;
}
/**
 * @ignore
 * 
 *
 * <example-url>../tinyplus3demo/#/discounts/discounts-all</example-url>
 */
@Component({
    selector: 'tp-discounts',
    templateUrl: './discounts.html',
    styleUrls: ['./discounts.less'],
    host: {
        '(click)': 'onClick($event)'
    }
})

export class TpDiscountsComponent extends TiBaseComponent {
    constructor(private elementRef: ElementRef, private render: Renderer2) {
        super(elementRef, render);
    }
    /**
     * 
     */
    @Input() discoutsConfig: TpDiscoutsConfig;
    /**
     * 
     *
     * 9.0.3 
     */
    @Output() readonly discountsChange: EventEmitter<any> = new EventEmitter();
    /**
     * @ignore
     */
    @ViewChild('font') fontEle: ElementRef;
    /**
     * @ignore
     */
    @ViewChild('container') containerEle: ElementRef;
    /**
     * @ignore
     */
    @ViewChild('before') beforeEle: ElementRef;
    /**
     * @ignore
     * 
     */
    public localeWords: any = TiLocale.getLocaleWords();
    /**
     * @ignore
     * 
     */
    public tpDiscounts: any = this.localeWords.tpDiscounts;
    /**
     * @ignore
     * ,
     */
    public isChinese: boolean = this.localeWords.tiLocaleKey === 'zh-CN';
    /**
     * @ignore
     * ,
     */
    public isOpen: boolean = false;
    /**
     * @ignore
     * ,
     */
    public sum: number;
    /**
     * @ignore
     * 
     */
    public ondemandDiscountsConfig: Array<TpDiscountDetail>;

    private onlistenClick: () => void;

    // tslint:disable-next-line:use-life-cycle-interface
    ngOnInit(): void {
        super.ngOnInit();
        if (this.discoutsConfig.type === 'ondemand') {
            this.handleDiscounts(this.discoutsConfig.discounts);
        }
        if (this.discoutsConfig.type === 'period') {
            this.setTotalSavings();
            this.setSelectableData(this.discoutsConfig.selectableDiscounts);
        }
        this.onlistenClick = this.render.listen(window, 'click', () => this.onDocumentClick());
    }

    // tslint:disable-next-line:use-life-cycle-interface
    ngOnChanges(changes: SimpleChanges): void {
        super.ngOnChanges(changes);
        if (this.discoutsConfig.type === 'ondemand') {
            this.handleDiscounts(this.discoutsConfig.discounts);
        }
        if (this.discoutsConfig.type === 'period') {
            const change: any = changes['discoutsConfig'];
            if (change && !change.isFirstChange()) {
                this.setTotalSavings();
            }
            this.setSelectableData(this.discoutsConfig.selectableDiscounts);
        }

    }

    // tslint:disable-next-line:use-life-cycle-interface
    ngAfterViewChecked(): void {
        super.ngAfterViewChecked();
        if (this.isOpen) {
            // 
            const tipModalIconWidth: number = this.fontEle.nativeElement.offsetWidth;
            // 
            // offselLeftsideBar47px
            // getBoundingClientRect().leftok.
            const tipModalIconLeft: number = this.fontEle.nativeElement.getBoundingClientRect().left;
            // 
            const tipModalIconRight: number = window.innerWidth - tipModalIconLeft - tipModalIconWidth;
            // 
            const tipContainerWidth: number = this.containerEle.nativeElement.offsetWidth;
            // 
            const offset: number = (tipContainerWidth - tipModalIconWidth) / 2;
            //  - 0
            const isLeftOverflow: boolean = (offset - tipModalIconLeft) > 0;
            //  - 0
            const isRightOverflow: boolean = (offset - tipModalIconRight) > 0;
            const distance: number = 20; // 20px
            let marginLeft: number;
            if (isLeftOverflow) {
                marginLeft = -tipModalIconLeft + distance;
            } else if (isRightOverflow) {
                marginLeft = -(tipContainerWidth - tipModalIconRight - tipModalIconWidth) - distance;
            } else {
                marginLeft = -offset;
            }
            this.render.setStyle(this.containerEle.nativeElement, 'margin-left', marginLeft + 'px');
            this.render.setStyle(this.beforeEle.nativeElement, 'left', tipModalIconWidth / 2 + 'px');
        }
    }

    // tslint:disable-next-line:use-life-cycle-interface
    ngOnDestroy(): void {
        super.ngOnDestroy();
        if (this.onlistenClick) {
            this.onlistenClick();
        }
    }

    /**
     * @ignore
     * radio
     */
    public onRadioChange($event: Event): void {
        this.setTotalSavings();
        this.discountsChange.emit($event);
    }
    /**
     * documnet
     */
    private onDocumentClick(): void {
        this.isOpen = false;
    }
    /**
     * @ignore
     * 
     */
    public onClose($event: any): void {
        this.isOpen = false;
        $event.stopPropagation();
    }
    /**
     * @ignore
     * tip
     */
    public onContainerClick($event: any): void {
        $event.stopPropagation();
    }
    /**
     * @ignore
     */
    public onClick($event: any): void {
        // A
        if (event.target['tagName'] === 'A') {
            return;
        }
        this.isOpen = !this.isOpen;
        $event.stopPropagation();
    }
    // 
    private getOndemandDiscounts(data: Array<TpDiscountDetail>): Array<TpDiscountDetail> {
        const out: Array<TpDiscountDetail> = [];
        for (let i: number = 0; i < data.length; i++) {
            const cur: TpDiscountDetail = data[i];
            const n: number = out.findIndex((ele: any) => ele.label === cur.label);
            if (n > -1) {
                const item: TpDiscountDetail = {
                    ...out[n],
                    ...cur,
                    ...{
                        expireTime: Util.isString(out[n].expireTime) && Util.isString(cur.expireTime) &&
                            new Date(out[n].expireTime).getTime() > new Date(cur.expireTime).getTime() ? cur.expireTime : out[n].expireTime,
                        type: this.getData(out[n].type, cur.type),
                        href: this.getData(out[n].href, cur.href)
                    }
                };
                out[n] = item;
            } else {
                out.push(cur);
            }
        }

        return out;
    }
    // tslint:disable-next-line:prefer-function-over-method
    private getData(val1: any, val2: any): any {
        if (Util.isUndefined(val2) || val2 === '') {
            return val1;
        }

        return val2;
    }

    // type
    // tslint:disable-next-line:prefer-function-over-method
    private setDetailLink(item: TpDiscountDetail): void {
        CfUtil.getLinks()
        .then((linksData: any) => {
            const user_center_common: string = linksData && linksData.links && linksData.links.user_center_common
                ? linksData.links.user_center_common : '';
            let detailLink: string = '';
            switch (item.type) {
                case 0:
                    detailLink = user_center_common + this.tpDiscounts.discounts_detailLink_type0;
                    break;
                case 1:
                    detailLink = user_center_common + this.tpDiscounts.discounts_detailLink_type1;
                    break;
                case 2:
                    detailLink = user_center_common + this.tpDiscounts.discounts_detailLink_type2
                    + (Util.isUndefined(item.beId) ? '' : '&beId=' + item.beId);
                    break;
                default:
                    detailLink = Util.isString(item.href) ? item.href : '';
                    break;
            }
            detailLink = this.addLinkParams(detailLink);
            item.href = detailLink;
        });
    }

    private getLink(value: Array<TpDiscountDetail>): void {
        value.forEach((item: TpDiscountDetail) => {
            this.setDetailLink(item);
        });
    }
    /**
     * @ignore
     * 
     */
    public showExpireTime(val: Date): boolean {
        const nowTime: number = new Date().getTime();
        const expireTime: number = new Date(val).getTime();
        const gap: number = expireTime - nowTime;

        return gap > 0 && gap < 86400000 * 7;
    }
    /**
     * @ignore
     * 
     */
    public getFormatExpireTime(val: Date): any {
        return CfUtil.formatLocaleDateTime(val);
    }

    // 
    private handleDiscounts(items: Array<TpDiscountDetail>): Array<any> {
        const ondemandDiscounts: Array<any> = [];
        for (let i: number = 0; i < items.length; i++) {
            //   8 20% off 
            items[i].discountRate = !Util.isNumber(items[i].rate) ? '' :
                this.isChinese ? ` ${parseFloat((items[i].rate * 10).toFixed(2))}${this.tpDiscounts.discounts}`
                : `; ${parseFloat(((1 - items[i].rate) * 100).toFixed(2))}%${this.tpDiscounts.discounts}`;
            ondemandDiscounts[i] = {
                rate: Util.isNumber(items[i].rate) ? items[i].rate : undefined,
                expireTime: Util.isString(items[i].expireTime) ? items[i].expireTime : '',
                type: Util.isNumber(items[i].type) ? items[i].type : undefined,
                beId: Util.isString(items[i].beId) ? items[i].beId : '',
                href: Util.isString(items[i].href) ? items[i].href : ''
            };

            let name: string;
            let resourceName: string;
            let specification: string;
            if (this.isChinese) {
                name = Util.isString(items[i].name) ? items[i].name : '';
                resourceName = Util.isString(items[i].resourceName) ? items[i].resourceName : '';
                specification = Util.isString(items[i].specification) ? items[i].specification : '';
            } else {
                name = !Util.isEmptyString(items[i].name) ? items[i].name : '';
                resourceName = !Util.isEmptyString(items[i].resourceName) ? '; ' + items[i].resourceName : '';
                specification = !Util.isEmptyString(items[i].specification) ? ', ' + items[i].specification : '';
            }
            ondemandDiscounts[i].label = name + resourceName + specification + items[i].discountRate;
        }
        this.ondemandDiscountsConfig = this.getOndemandDiscounts(ondemandDiscounts);
        this.getLink(this.ondemandDiscountsConfig);

        return this.ondemandDiscountsConfig;
    }

    // 
    private getSelectableDetail(vals: Array<TpDiscountDetail>): void {
        for (const val of vals) {
            if (!Util.isUndefined(val.href)) {
                return;
            }
            this.setDetailLink(val);
        }
    }
    // agencyIDlocale
    private addLinkParams(url: string): string {
        if (url === '') {
            return url;
        }
        const storage: any = CfUtil.getStorageService();
        if (!(!Util.isUndefined(storage) && !Util.isUndefined(storage.cookieStorage) && Util.isFunction(storage.cookieStorage.getItem))) {
            return url;
        }
        const agencyID: any = storage.cookieStorage.getItem('agencyID');
        const locale: any = storage.cookieStorage.getItem('locale');
        let finalUrl: string = url;
        if (!Util.isEmptyString(agencyID)) {
            finalUrl = finalUrl + '&agencyId=' + agencyID;
        }
        if (!Util.isEmptyString(locale)) {
            finalUrl = finalUrl + '&locale=' + locale;
        }

        return finalUrl;
    }

    /**
     * 
     */
    private setTotalSavings(): void {
        this.sum = 0;
        if (Util.isArray(this.discoutsConfig.selectedDiscounts)) {
            for (const item of this.discoutsConfig.selectedDiscounts) {
                if (!Util.isNumber(item.priceNum)) {
                    continue;
                } else {
                    this.sum += item.priceNum;
                }
            }
        }
        // index
        if (!Util.isUndefined(this.discoutsConfig.selectableDiscounts)) {
            const index: number = this.discoutsConfig.selectableDiscounts.findIndex((ele: any) => ele.id === this.discoutsConfig.selectedId)
            const selectedDiscountsNum: number = this.discoutsConfig.selectableDiscounts[index]
                && this.discoutsConfig.selectableDiscounts[index].priceNum;
            if (index > -1 && Util.isNumber(selectedDiscountsNum)) {
                this.sum += selectedDiscountsNum;
            }
        }
    }
    /**
     * @ignore
     *
     * 00
     */
    public isSumEqualZero(): boolean {
        this.setTotalSavings();
        const reg: RegExp = new RegExp('[\.-]');
        const precision: number = this.discoutsConfig.numFormat ?
        Number(this.discoutsConfig.numFormat.split(reg)[2]) : 2;

        return parseFloat(this.sum.toFixed(precision)) === 0;
    }
    /**
     * 
     */
    private setSelectableData(options: Array<TpDiscountDetail>): void {
        if (!Util.isArray(options) || options.length === 0) {
            return;
        }
        this.getSelectableDetail(options);
            const length: number = options.length;
            if (options[length - 1].id !== 'no-discounts') {
                options[length] = {
                    label: this.tpDiscounts.no_discounts,
                    id: 'no-discounts',
                    href: '',
                    priceNum: 0
                };
            }
    }
}
