import { Component, ContentChild, ElementRef, EventEmitter, Input, Output, Renderer2, SecurityContext, TemplateRef, ViewChild } from '@angular/core';
import { DomSanitizer } from '@angular/platform-browser';
import { TiBaseComponent, TiBrowser, TiLocale, Util } from '@cloud/tiny3';

// valuesURLvalueURL
// 

export interface TpPricecalcParam {
    value: string;
    key: string;
}

/**
 * 
 *
 * <example-url>../tinyplus3demo/#/pricecalc/pricecalc-all</example-url>
 */
@Component({
    selector: 'tp-pricecalc',
    templateUrl: './pricecalc.html',
    styleUrls: ['./pricecalc.less']
})
export class TpPricecalcComponent extends TiBaseComponent {
    /**
     * 
     *
     *  : /price Calculator
     */
    @Input() label: string = TiLocale.getLocaleWords().tpPricecalc.label;
    /**
     * 
     *
     */
    @Input() detailHref: string;

    /**
     * 
     *
     * keyvalueurlkey1=value1&key2=value2
     */
    @Input() values: Array<TpPricecalcParam>;
    /**
     * 
     */
    @Input() url: string = 'http://www.helpcenter.com/';
    /**
     * url
     */
    @Input() method: string;
    /**
     * 
     */
    @Output() readonly beforeClick: EventEmitter<any> = new EventEmitter<any>();
    /**
     * @ignore
     */
    @ViewChild('myform', { static: true }) form: ElementRef;
    /**
     * @ignore
     */
    @ContentChild('detail', { static: true }) detailTemplate: TemplateRef<any>;
    /**
     * @ignore
     */
    public detailHrefBeforeText: string = TiLocale.getLocaleWords().tpPricecalc.detailHrefBeforeText;
    /**
     * @ignore
     */
    public detailHrefText: string = TiLocale.getLocaleWords().tpPricecalc.detailHrefText;
    /**
     * @ignore
     */
    public detailHrefAfterText: string = TiLocale.getLocaleWords().tpPricecalc.detailHrefAfterText;
    /**
     * @ignore
     */
    public isNotDefaultUrl: boolean;
    /**
     *  form 
     */
    private blankTab: string;

    constructor(elementRef: ElementRef, renderer2: Renderer2, private domSanitizer: DomSanitizer) {
        super(elementRef, renderer2);
    }
    /**
     * @ignore
     */
    public onClick(): void {
        this.beforeClick.emit();

        setTimeout(() => {
            //  form  rel="noopener noreferrer" 
            //  window.open (this.blankTab) form  target , 
            //          window(window.open ) opener  null
            // IE, IE 
            if (!TiBrowser.isIE()) {
                this.blankTab = Util.getUniqueId('new_tab'); // 
                const newWindow: any = window.open(this.domSanitizer.sanitize(SecurityContext.URL, this.url), this.blankTab);
                if (newWindow) {
                    newWindow.opener = null;
                }
            }
            this.form.nativeElement.target = TiBrowser.isIE() ? '_blank' : this.blankTab;
            this.form.nativeElement.submit();
        }, 0);
    }

    ngOnInit(): void {
        super.ngOnInit();
        this.isNotDefaultUrl = !!this.values && this.url !== 'http://www.helpcenter.com/';
    }
}
