import { Component, ContentChild, EventEmitter, Input, Output, TemplateRef } from '@angular/core';
import { getLocaleCurrencySymbol } from '@angular/common';
import { TiBaseComponent, TiLocale, Util } from '@cloud/tiny3';

import { CfUtil } from '../../cfutils/CfUtil';
import { TpDiscoutsConfig } from '../discounts/TpDiscountsModule';

import { TpUtil } from './../../utils/TpUtil';

export interface TpCosts {
    /**
     * label
     */
    label?: string;
    /**
     * 
     */
    value?: number | string;
    /**
     * 
     */
    measureUnit?: string;
    /**
     * 
     */
    isBeta?: boolean;
    /**
     * 
     */
    discount?: number;
    /**
     * 
     */
    link?: string;
    /**
     * 
     */
    majorPrice?: boolean;
    /**
     * 
     *
     * 10.1.0 
     */
    hide?: boolean;
    /**
     * 
     *
     * 10.1.0 
     */
    hideConnectSymbol?: boolean;
    /**
     * 
     */
    pricelist?: Array<any>;
    /**
     * @ignore
     */
    tipTitle?: string;
    /**
     * 
     *
     * 10.1.2
     */
    [propName: string]: any;
}

export interface TpCalculator {
    /**
     * 
     */
    url?: string;
    /**
     * 
     */
    [propName: string]: any;
}

/**
 * @ignore
 * 
 * <example-url>../tinyplus3demo/#/buylayer/buylayer-all</example-url>
 */
@Component({
    selector: 'tp-buylayer-left',
    templateUrl: './buylayer-left.html',
    styleUrls: ['./buylayer-left.less']
})

export class TpBuylayerLeftComponent extends TiBaseComponent {
    /**
     * 
     */
    @Input() costs: Array<TpCosts> = [];
    /**
     * ()discounts
     */
    @Input() majorDiscoutsConfig: TpDiscoutsConfig;
    /**
     * discounts
     */
    @Input() minorDiscoutsConfig: TpDiscoutsConfig;
    /**
     * 
     */
    @Input() showFreechargeDetail: boolean;
    /**
     * 
     */
    @Input() costExplain: string;
    /**
     * 
     */
    @Input() calculator: TpCalculator;
    /**
     * 
     * 1.2-4124
     * 
     */
    @Input() numFormat: string;
    /**
     * 
     */
    @Input() detailHref: string;
    /**
     * @ignore
     * 
     */
    @Input() isRefund: boolean;
    /**
     * tip
     * 10.1.4
     */
    @Input() detailTipMaxWidth: string;
    /**
     * 
     *
     * 9.0.3 
     */
    @Output() readonly discountsChange: EventEmitter<any> = new EventEmitter();

    @ContentChild(TemplateRef) leftZoneEle1: TemplateRef<any>;

    /**
     * @ignore
     */
    public freechargeDetailLink: string = '';
    /**
     * @ignore
     * tip
     */
    public pricelistHead: string = TiLocale.getLocaleWords().tpBuylayer.buyLayer_cost_pricelist;
    /**
     * @ignore
     */
    public majorPriceNum: number;

    /**
     * @ignore
     * tip
     */
    public onlyHasMajorprice: boolean;
    /**
     * @ignore
     * 
     */
    public hasNumberofMajorprice: boolean = false;
    /**
     * @ignore
     * 
     */
    public showMajorDiscount: boolean;
    /**
     * @ignore
     * 
     */
    public showMinorDiscount: boolean;
    /**
     * @ignore
     * 
     */
    public isMajorPriceAllHide: boolean;
    /**
     * @ignore
     * 
     */
    public isMinorPriceAllHide: boolean;
    /**
     * @ignore
     * 
     */
    public finalCosts: Array<TpCosts>;
    // tslint:disable-next-line:use-life-cycle-interface
    ngOnInit(): void {
        super.ngOnInit();
        const routeLink: string = TiLocale.getLocaleWords().tpBuylayer.buyLayer_freeCharge_detailLink;

        this.costsChange();

        this.discountChange();

        if (!Util.isUndefined(this.numFormat) && !Util.isUndefined(this.majorDiscoutsConfig)) {
            // 
            this.majorDiscoutsConfig.numFormat = this.numFormat;
        }

        CfUtil.getLinks()
            .then((linksData: any) => {
            if (linksData && linksData.links && linksData.links.user_center_common) {
                this.freechargeDetailLink = this.addLinkParams(linksData.links.user_center_common + routeLink);
            }
        });
    }

    // tslint:disable-next-line:use-life-cycle-interface
    ngOnChanges(): void {
        this.costsChange();
        this.discountChange();
    }

    ngDoCheck(): void {
        if (!Util.isArray(this.costs) || this.costs.length === 0) {
            return;
        }
        // costshide
        const currentMajorCosts = this.costs.filter((item: TpCosts) => !item.hide && item.majorPrice);
        const currentMinorCosts = this.costs.filter((item: TpCosts) => !item.hide && !item.majorPrice);
        currentMajorCosts.forEach((item: TpCosts, index: number) => {
            item.hideConnectSymbol = !index;
        })
        currentMinorCosts.forEach((item: TpCosts, index: number) => {
            item.hideConnectSymbol = !index;
        });
    }
    // 
    private costsChange(): void {
        this.setMajorPrice();
        this.finalCosts = this.costs.filter((item: TpCosts) => !item.hide);
        this.majorPriceNum = this.getMajorPriceNum();
        this.isCostsAllHide();
        if (this.isRefund) {
            this.setRefundParams();
        }

        this.onlyHasMajorprice = this.finalCosts && this.finalCosts.length > 0 && this.majorPriceNum === this.finalCosts.length ? true : false;
    }

    private hasDiscountsData(config: TpDiscoutsConfig): boolean {
        const hasSelectableDiscounts: boolean = Util.isArray(config.selectableDiscounts) && config.selectableDiscounts.length > 0;
        const hasSelectedDiscounts: boolean = Util.isArray(config.selectedDiscounts) && config.selectedDiscounts.length > 0;
        const hasDiscounts: boolean = Util.isArray(config.discounts) && config.discounts.length > 0;

        return hasDiscounts || (hasSelectedDiscounts || hasSelectableDiscounts);
    }

    private discountChange(): void {
        if (TpUtil.isObject(this.majorDiscoutsConfig) && this.hasDiscountsData(this.majorDiscoutsConfig) && this.majorPriceNum > 0) {
            this.showMajorDiscount = true;
        } else {
            this.showMajorDiscount = false;
        }

        const minorpriceNum: number = this.costs && this.costs.length - this.majorPriceNum; // 
        if (TpUtil.isObject(this.minorDiscoutsConfig) && this.hasDiscountsData(this.minorDiscoutsConfig) && minorpriceNum > 0) {
            this.showMinorDiscount = true;
        } else {
            this.showMinorDiscount = false;
        }
    }
    /**
     * @ignore
     */
    public change($event: Event): void {
        this.discountsChange.emit($event);
    }
    // 
    private getMajorPriceNum(): number {
        if (this.finalCosts && this.finalCosts.length > 0) {
            let num: number = 0;
            for (const cost of this.finalCosts) {
                if (cost.majorPrice) {
                    num++;
                    if (this.isNumber(cost.value)) {
                        this.hasNumberofMajorprice = true;
                    }
                }
            }

            return num;
        }

        return 0;
    }
    // /
    private isCostsAllHide() {
        if (this.costs && this.costs.length > 0) {
            // 
            const majorPriceArr = this.costs.filter((item) => item.majorPrice);
            // 
            const minorPriceArr = this.costs.filter((item) => !item.majorPrice);
            this.isMinorPriceAllHide = this.isAllHide(minorPriceArr);
            this.isMajorPriceAllHide = this.isAllHide(majorPriceArr);
        }
    }
    private isAllHide(arr): boolean {
        let isAllHide = true;
        if (arr.length > 0) {
            for (const cost of arr) {
                if(!cost.hide) {
                    isAllHide = false;
                }
            }
        }
        return isAllHide;
    }
    // 
    private setMajorPrice(): void {
        if (this.costs && this.costs.length > 0) {
            for (const cost of this.costs) {
                if (Util.isUndefined(cost.label) && !this.isRefund) {
                    cost.label = TiLocale.getLocaleWords().tpBuylayer.buyLayer_cost_label;
                    cost.tipTitle = TiLocale.getLocaleWords().tpBuylayer.buyLayer_cost_label;
                }
                if (Util.isUndefined(cost.majorPrice)) {
                    cost.majorPrice = true;
                }
            }
        }
    }
    /**
     * 
     */
    private setRefundParams(): void {
        if (this.costs && this.costs.length > 0) {
            for (const cost of this.costs) {
                if (Util.isUndefined(cost.label)) {
                    cost.label = TiLocale.getLocaleWords().tpBuylayer.buyLayer_cost_label_refund_majorprice;
                    cost.tipTitle = TiLocale.getLocaleWords().tpBuylayer.buyLayer_cost_label_refund_detail;
                }

                if (cost.pricelist && cost.pricelist.length > 0) {
                    cost.pricelist[0].title = TiLocale.getLocaleWords().tpBuylayer.buyLayer_cost_pricelist_label_before;
                    cost.pricelist[1].title = TiLocale.getLocaleWords().tpBuylayer.buyLayer_cost_pricelist_label_after;
                    cost.pricelist[2] = {
                        title: TiLocale.getLocaleWords().tpBuylayer.buyLayer_cost_label_refund,
                        priceNum: cost.value
                    };

                    this.pricelistHead = TiLocale.getLocaleWords().tpBuylayer.buyLayer_cost_type;
                }
            }
        }
    }
    /**
     * @ignore
     */
    public isNumber(val: any): boolean {
        return Util.isNumber(val);
    }
    /**
     * @ignore
     */
    public isPositiveNumber(val: any): boolean {
        return Util.isNumber(val) && val > 0;
    }
    /**
     * @ignore
     * 
     */
    public getLocalSymble(): any {
        const localeKey: string = TiLocale.getLocaleWords().tiLocaleKey;

        return getLocaleCurrencySymbol(localeKey);
    }

    // agencyIDlocale
    private addLinkParams(url: string): string {
        if (url === '') {
            return url;
        }
        const storage: any = CfUtil.getStorageService();
        if (!(!Util.isUndefined(storage) && !Util.isUndefined(storage.cookieStorage) && Util.isFunction(storage.cookieStorage.getItem))) {
            return url;
        }
        const agencyID: any = storage.cookieStorage.getItem('agencyID');
        const locale: any = storage.cookieStorage.getItem('locale');
        let finalUrl: string = url;
        if (!Util.isEmptyString(agencyID)) {
            finalUrl = finalUrl + '?agencyId=' + agencyID;
            if (!Util.isEmptyString(locale)) {
                finalUrl = finalUrl + '&locale=' + locale;
            }
        } else if (!Util.isEmptyString(locale)) {
            finalUrl = finalUrl + '?locale=' + locale;
        }

        return finalUrl;
    }

}
