// tslint:disable-next-line: no-implicit-dependencies
import { Component, ElementRef, Input, Renderer2, SimpleChange, SimpleChanges, ViewChild } from '@angular/core';
// tslint:disable-next-line: no-implicit-dependencies
import { TiButtongroupComponent, TiButtonItem, TiFormComponent, TiLocale, Util } from '@cloud/tiny3';

/**
 * @ignore
 * 
 */
export interface TpPricelineRect {
    x: number;
    y: number;
    width: number;
    height: number;
    active: boolean;
    text: string;
    /**
     * 
     */
    [propName: string]: any;

}
/**
 * 
 */
export interface TpPricelineItem extends TiButtonItem {
    /**
     * 
     */
    price: number;
}

/**
 *  , tiny2btnSliderprice-option
 *
 *  9.0.4
 *
 * <example-url>../tinyplus3demo/#/priceline/priceline-all</example-url>
 */
@Component({
    selector: 'tp-priceline',
    templateUrl: './priceline.html',
    styleUrls: ['./priceline.less'],
    providers: [TiFormComponent.getValueAccessor(TpPricelineComponent)]
})
export class TpPricelineComponent extends TiFormComponent {

    /**
     * 
     */
    @Input() items: Array<TpPricelineItem> = [];
    /**
     * 
     */
    @Input() deselectable: boolean = false;
    /**
     * @ignore
     * canvas 
     */
    @ViewChild('canvas') canvasRef: ElementRef;
    /**
     * @ignore
     * canvas 
     */
    @ViewChild('canvasWrap') canvasWrapRef: ElementRef;
    /**
     * @ignore
     * 
     */
    @ViewChild(TiButtongroupComponent) buttonGroupCom: TiButtongroupComponent;

    // 
    private ctx: CanvasRenderingContext2D;
    // canvas 
    private canvasOption: any;
    private canvasWidth: number;
    // 
    private step: number;
    // items 
    private previousItems: Array<any>;
    // 
    private rects: Array<TpPricelineRect>;
    // 
    private x: number;
    private y: number;
    private lastX: number;
    private lastButton: DOMRect;
    // items[0].price
    private maxPrice: number;
    // IE11 CSSVar initCanvas
    private timer: any;

    constructor(hostRef: ElementRef, renderer: Renderer2) {
        super(hostRef, renderer);
    }

    // tslint:disable-next-line: use-life-cycle-interface
    ngAfterViewInit(): void {
        super.ngAfterViewInit();
        this.initCanvas();
        if (this.canvasOption) {
            // setTimeout
            setTimeout(() => {
                this.draw(this.items);
            }, 0);
        } else {
            this.timer = setInterval(() => {
                this.initCanvas();
                if (this.canvasOption) {
                    this.draw(this.items);
                    clearInterval(this.timer);
                }
               // IE1140msURL80ms100
            }, 100);
        }

        // tslint:disable-next-line: newline-per-chained-call
        this.setFocusableElems(this.buttonGroupCom.getFocusableElems().concat(this.nativeElement));
    }

    // tslint:disable-next-line: use-life-cycle-interface
    ngOnChanges(changes: SimpleChanges): void {
        // 
        const items: SimpleChange = changes['items'];
        if (items && !items.firstChange) {
            this.step = 10;
            this.previousItems = items.previousValue;
            this.changeDraw();
        }
    }

    onNgModelChange(data: TiButtonItem): void {
        // 
        if (this.model) {
            this.draw(this.items);
        }
    }

    ngOnDestroy(): void {
        super.ngOnDestroy();
        clearInterval(this.timer);
    }
    // 
    // 200ms20ms10
    // todo 1.16.66 602. requestAnimationFrame  setTimeout
    private changeDraw(): void {
        // items
        const currentItems: Array<TiButtonItem> = this.items.map((item: any, index: number) => {
            // 
            const newItem: TiButtonItem = { ...item };
            newItem.price = newItem.price - (newItem.price - this.previousItems[index].price) * (this.step / 10);

            return newItem;
        });

        this.draw(currentItems);
        this.step--;
        // 20 20ms
        setTimeout(() => {
            if (this.step > -1) {
                this.changeDraw();
            }
        }, 20);

    }

    //  canvas
    private initCanvas(): void {

        let lineColor: string;
        let color: string;
        let activeColor: string;
        let startColor: string;
        let endColor: string;

        if (Util.supportsCssVars()) {
            // ChromeCSSVar,  CSSVar
            const cSSStyleDeclaration: CSSStyleDeclaration = getComputedStyle(document.documentElement);
            lineColor = cSSStyleDeclaration.getPropertyValue('--ti-common-color-line-active');
            color = cSSStyleDeclaration.getPropertyValue('--ti-common-color-text-weaken');
            activeColor = cSSStyleDeclaration.getPropertyValue('--ti-common-color-text-primary');
            startColor = cSSStyleDeclaration.getPropertyValue('--ti-common-color-bg-emphasize');
            endColor = startColor;
        } else {
            // IEEdgeCSSVarCSS
            const cSSStyleDeclaration: CSSStyleDeclaration = getComputedStyle(this.canvasRef.nativeElement);
            color = cSSStyleDeclaration.getPropertyValue('flood-color');
            activeColor = cSSStyleDeclaration.getPropertyValue('lighting-color');

            const cSSStyleDeclaration2: CSSStyleDeclaration = getComputedStyle(this.canvasWrapRef.nativeElement);
            lineColor = cSSStyleDeclaration2.getPropertyValue('flood-color');
            startColor = cSSStyleDeclaration2.getPropertyValue('lighting-color');
            endColor = cSSStyleDeclaration2.getPropertyValue('stop-color');
            // tslint:disable-next-line: max-line-length
        }

        if (!lineColor || !color || !activeColor || !startColor || !endColor) {

            return;
        }

        // canvas
        this.canvasOption = {
            // 
            height: 60,
            carveHeight: 40,
            // 1
            lineWidth: 1.4,
            // chrome  
            lineColor: lineColor.trim(),
            title: TiLocale.getLocaleWords().tpPriceline.carveTitle,
            color: color.trim(),
            titleX: 0,
            titleY: 14,
            fontSize: '12px',
            fontFamily: 'Roboto,Microsoft YaHei,,Sans-serif',
            activeColor: activeColor.trim(),
            tagOffset: 6,
            tagUnit: '/' + TiLocale.getLocaleWords().tpPriceline.tagUnit,
            tagPrefix: TiLocale.getLocaleWords().tpPriceline.tagPrefix,
            currency: TiLocale.getLocaleWords().tpPriceline.tagCurrency,
            startColor: this.color2rgba(startColor.trim(), 0.26),
            endColor: this.color2rgba(endColor.trim(), 0)
        };

        // canvas
        this.renderer.setAttribute(this.canvasRef.nativeElement, 'height', this.canvasOption.height);

        // canvas canvasIE11 ngAfterViewInit
        const wrapStyles: CSSStyleDeclaration = getComputedStyle(this.canvasWrapRef.nativeElement);
        this.canvasWidth = parseInt(wrapStyles.width, 10);
        this.renderer.setAttribute(this.canvasRef.nativeElement, 'width', String(this.canvasWidth));

        this.ctx = (this.canvasRef.nativeElement as HTMLCanvasElement).getContext('2d');
    }

    // ie11 canvas # rgba
    // tslint:disable-next-line: prefer-function-over-method
    private color2rgba(hex: string, alpha?: number): string {
        const colorRegex: RegExp = /^#?[a-fA-F0-9]+$/;
        if (!colorRegex.test(hex) || hex.length !== 7) {
            return hex;
        }
        const values: Array<number> = [
            parseInt(hex.slice(1, 3), 16),
            parseInt(hex.slice(3, 5), 16),
            parseInt(hex.slice(5), 16)
        ];
        if (alpha >= 0 && alpha <= 1) {
            values.push(alpha);
        } else {
            values.push(1);
        }

        return `rgba(${values.join(',')})`;
    }
    // 1 2.itemspricerects
    // 3. rects, 4. rects
    private draw(items: Array<any>): void {
        if (!items || items.length < 2 || !this.canvasOption) {
            return;
        }
        // 
        this.ctx.clearRect(0, 0, this.canvasWidth, this.canvasOption.height);
        this.rects = [];
        this.x = 0;
        this.lastX = 0;
        this.maxPrice = items[0].price;
        // tslint:disable-next-line: prefer-for-of
        for (let index: number = 1; index < this.items.length; index++) {
            if (items[index].price > this.maxPrice) {
                this.maxPrice = items[index].price;
            }
        }
        this.y = this.canvasOption.height - this.canvasOption.carveHeight * items[0].price / this.maxPrice;

        const buttons: NodeListOf<Element> = (this.nativeElement as HTMLElement).querySelectorAll('.ti3-btn-item-radio');
        //  
        this.lastButton = buttons[0].getBoundingClientRect();

        // 
        this.drawText({
            x: this.canvasOption.titleX,
            y: this.canvasOption.titleY,
            text: this.canvasOption.title
        });

        // 
        const len: number = this.items.length;
        for (let index: number = 1; index < len; index++) {

            // 
            if (index !== len - 1 && this.items[index].price === this.items[index - 1].price) {
                continue;
            }

            const button: DOMRect = buttons[index].getBoundingClientRect();
            // 2
            if (index === len - 1 && items[index].price !== items[index - 1].price) {
                this.x = this.x + (button.left - this.lastButton.left);
                this.record(items, index);
                // 
                this.lastX = this.x;
                this.y = this.canvasOption.height - this.getCurrentHeight(items[index].price, items);
                this.lastButton = button;
                this.x = this.x + button.width;
                this.record(items, index, true);
                // 
            } else if (index === len - 1 && items[index].price === items[index - 1].price) {
                this.x = this.x + ((button.left + button.width) - this.lastButton.left);
                this.record(items, index, true);
                // 
            } else {
                this.x = this.x + button.left - this.lastButton.left;
                this.record(items, index);
                this.lastX = this.x;
                this.y = this.canvasOption.height - this.getCurrentHeight(items[index].price, items);
                this.lastButton = button;
            }

        }

        // 
        this.rects.forEach((rect: TpPricelineRect) => {
            this.drawRect(rect);
        });

        // 
        this.drawLine(this.ctx, { width: this.canvasOption.lineWidth, color: this.canvasOption.lineColor });
    }

    // 
    private getCurrentHeight(curPrice: number, items: Array<any>): number {
        return this.canvasOption.carveHeight * curPrice / this.maxPrice;
    }

    // 
    private record(items: Array<any>, currrentIndex: number, isEnd?: boolean): void {
        const price: number = this.items[isEnd ? currrentIndex : currrentIndex - 1].price;
        // 
        const rectOption: TpPricelineRect = {
            x: this.lastX,
            y: this.y,
            width: isEnd ? (this.x - this.lastX) : (this.x - this.lastX - 2),
            height: this.canvasOption.height - this.y,
            // tslint:disable-next-line: max-line-length
            text: `${this.rects.length === 0 ? this.canvasOption.tagPrefix : ''}${this.canvasOption.currency}${price.toFixed(2)}${this.canvasOption.tagUnit}`,
            active: price === this.model.price
        };
        this.rects.push(rectOption);
    }

    // canvas 
    private drawText(config: any): void {
        const option: any = {
            offset: this.canvasOption.tagOffset,
            color: this.canvasOption.color,
            fontSize: this.canvasOption.fontSize,
            fontFamily: this.canvasOption.fontFamily,
            ...config
        };

        this.ctx.fillStyle = option.active ? this.canvasOption.activeColor : this.canvasOption.color;
        // font 
        this.ctx.font = `${option.fontSize} ${option.fontFamily}`;

        if (option.alignRight) {
            const textMetrics: TextMetrics = this.ctx.measureText(option.text);
            option.x = (option.x + option.width) - textMetrics.width;
            option.y = option.y - 6;
        }
        this.ctx.fillText(option.text, option.x, option.y);
    }
    // 
    private drawLine(ctx: CanvasRenderingContext2D, option: { width: number, color: string }): void {
        ctx.lineJoin = 'round';
        ctx.lineWidth = option.width;
        ctx.strokeStyle = option.color;
        ctx.beginPath();

        for (let index: number = 0; index < this.rects.length; index++) {
            const rect: TpPricelineRect = this.rects[index];

            // 
            if (index === 0) {
                ctx.moveTo(Math.round(rect.x), Math.round(rect.y));
            } else {
                // 
                ctx.lineTo(Math.round(rect.x - 1), Math.round(rect.y));
            }
            // 
            ctx.lineTo(Math.round(rect.x + rect.width + 1), Math.round(rect.y));
        }

        ctx.stroke();
    }
    // 
    private drawRect(option: TpPricelineRect): void {
        // tslint:disable-next-line: max-line-length
        const canvasGradient: CanvasGradient = this.ctx.createLinearGradient(0, this.canvasOption.height - this.canvasOption.carveHeight, 0, this.canvasOption.height);
        canvasGradient.addColorStop(0, this.canvasOption.startColor);
        canvasGradient.addColorStop(1, this.canvasOption.endColor);
        this.ctx.fillStyle = canvasGradient;
        this.ctx.fillRect(option.x, option.y, option.width, option.height);
        this.drawText({ ...option, alignRight: true });
    }
}
