import { Component, EventEmitter, Input, Output } from '@angular/core';
import { TiBaseComponent, TiPositionType } from '@cloud/tiny3';

export interface TpResourceItem {
    /* tslint:disable:no-redundant-jsdoc */
    /**
     * 
     */
    name: string;
    /**
     * 
     */
    total?: number;
    /**
     * 
     */
    error?: number;
    /**
     * 
     * @default 99
     */
    errorMax?: number;
    /**
     * 
     *
     * contentname
     *
     * position'top'
     */
    nameTip?: TpResourceTip;
    /**
     * 
     *
     * content
     *
     * position 'bottom-left'
     */
    totalTip?: TpResourceTip;
    /**
     * :
     *
     * content
     *
     * position 'bottom-left'
     */
    errorTip?: TpResourceTip;
    /**
     * ;
     * @default false
     */
    hide?: boolean;
    /**
     * 
     */
    [propName: string]: any;
}

export interface TpResourceTip {
    /**
     * 
     */
    content?: string;
    /**
     * auto
     *
     * "top" | "top-left" | "top-right" | "bottom" | "bottom-left" | "bottom-right" | "left" | "left-top" | "left-bottom" | "right" | "right-top" | "right-bottom" | "center" | "auto" | "none"
     */
    position?: TiPositionType;
}

/**
 * @ignore
 */
@Component({
    selector: 'tp-resource-item',
    templateUrl: './resource-item.html',
    styleUrls: ['./resource-item.less'],
    // host: {
    //     '[class.tp-resource-item]': 'true'
    // }
})

export class TpResourceItemComponent extends TiBaseComponent {
    /**
     * 
     */
    @Input() item: TpResourceItem;

    /**
     * 
     */
    @Output() readonly errorSelect: EventEmitter<TpResourceItem> = new EventEmitter<TpResourceItem>();

    /**
     * @ignore
     * 
     */
    public getErrorNumber(error: number, errorMax: number): any {
        const baseValue: any = errorMax && errorMax > 0 ? errorMax : 99;

        return error > baseValue ? `${baseValue}+` : error;
    }

    /**
     * @ignore
     */
    public onClickError(item: TpResourceItem, event: Event): void {
        // 
        event.stopPropagation();
        this.errorSelect.emit(item);
    }
}
