import { Component, EventEmitter, Input, Output } from '@angular/core';
import { Params } from '@angular/router';
import { TiBaseComponent } from '@cloud/tiny3';

export interface TpLink {
    /* tslint:disable:no-redundant-jsdoc */
    /**
     * ;
     */
    label?: string;
    /**
     * ;
     */
    href?: string;
    /**
     * 
     * @default '_self'
     */
    target?: '_self' | '_blank' | '_parent' | '_top';
    /**
     * 
     * 10.0.4 
     */
    routerLink?: string | Array<any>;
    /**
     * 
     * 10.1.0 
     */
    queryParams?: Params
    /**
     * 
     */
    [propName: string]: any;
}

/**
 *  crumb
 *
 * href;
 * 
 *
 * <example-url>../tinyplus3demo/#/crumb/crumb-all</example-url>
 */

@Component({
    selector: 'tp-crumb',
    templateUrl: './crumb.html',
    styleUrls: ['./crumb.less']
})

export class TpCrumbComponent extends TiBaseComponent {
    /**
     * 
     */
    @Input() items: Array<TpLink>;
    /**
     * href
     */
    @Output() readonly select: EventEmitter<TpLink> = new EventEmitter<TpLink>();

    /**
     * @ignore
     */
    public onClick(item: TpLink): boolean {
        this.select.emit(item);
        // aclickfalse
        if (!item.href) {
            return false;
        }
    }
}
