import { Component, Input, ViewChild } from '@angular/core';
import { TiBaseComponent, TiLocale, Util } from '@cloud/tiny3';

import { CfUtil } from '../../cfutils/CfUtil';
import { TpHalfmodalComponent } from '../halfmodal/TpHalfmodalModule';

import { TpFeedbackContentComponent } from './TpFeedbackContentComponent';
import { TpFeedbackOptions } from './TpFeedbackInterface';
import { TpUtil } from './../../utils/TpUtil';
/**
 *  feedback
 *
 * 
 *
 * 
 *
 * 1.console@cloud/cfdata;<br>
 * 2.options.allQuestionoptions.customQuestioncustomQuestionallQuestionCBCIAM<br>
 * 3.<b>8888</b>NPShttp://localhost:8888 <br>
 * 4.9010.1.0 3190MILLISECONDS_PER_MONTH(N)<br>
 * 5.NPSNPS 30009929"<br>
 * 6.http://csbi.huawei.com/#/home <b>serviceId</b>
 *
 * <example-url>../tinyplus3demo/#/feedback/feedback-all</example-url>
 */

@Component({
    selector: 'tp-feedback',
    templateUrl: './feedback.html',
    styleUrls: ['./feedback.less'],
    host: {
        '[hidden]': '!showFeedBack'
    }
})

export class TpFeedbackComponent extends TiBaseComponent {
    /**
     * 
     */
    @Input() options: TpFeedbackOptions;
    /**
     * @ignore
     */
    @ViewChild('halfModal', { static: true }) halfmodalCom: TpHalfmodalComponent;

    // 
    private CONST_ANT: any = {
        MILLISECONDS_PER_MONTH: 90 * 24 * 60 * 60 * 1000 // 90
    };

    /**
     * @ignore
     * 
     */
    public showFeedBack: boolean = false;

    /**
     * @ignore
     * 
     */
    public openModalAlert: boolean = false;

    /**
     * @ignore
     * 
     */
    public alertLinkText: string;
    private useId: string = '';

    ngOnInit(): void {
        super.ngOnInit();
        CfUtil.getUser()
            .then((user: any) => {
                const root: any = user;
                const camel: any = CfUtil.getHttpService();
                const msgService: any = CfUtil.getMessage();

                if (!camel || !Util.isFunction(camel.get) || !root || !msgService || !Util.isFunction(msgService.showSuccess)) {
                    return;
                }

                this.alertLinkText = Util.formatEntry(
                    TiLocale.getLocaleWords().tpFeedback.open_halfModal.click, [this.options.serviceName]);

                const useId: string = root.domainId;
                const url: string = `${TpFeedbackContentComponent.config.backend}/api/get/commit/date?surveyId={survey_id}&userId={user_id}&serviceId={service_id}`;
                this.useId = useId;
                if (this.useId) {
                    camel.get({
                        url: {
                            s: url,
                            o: {
                                survey_id: TpFeedbackContentComponent.config.surveyId,
                                user_id: this.useId, // domainIduserId
                                service_id: this.options.serviceId
                            }
                        }
                    })
                        .then((response: any) => {
                            // yyyy-MM-ddIEDate.parseNaNyyyy/mm/dd
                            const submitDate: number = Date.parse(response.data.replace(/-/g, '/'));

                            // ''0.NaN
                            const milliSeconds: number = isNaN(submitDate) ? 0 : submitDate;

                            // 90
                            if (new Date().getTime() - milliSeconds > this.CONST_ANT.MILLISECONDS_PER_MONTH) {
                                const tinyVocValue: string = Util.isUndefined(TpUtil.getCookie('tinyVoc')) ? '' : TpUtil.getCookie('tinyVoc');
                                if (tinyVocValue.indexOf(`${this.options.serviceId}${this.useId}`) === -1) {
                                    this.showFeedBack = true;
                                    this.openModalAlert = true;
                                }
                            }
                        }, () => {
                            // 
                        });
                }
            });
    }

    /**
     * @ignore
     * 
     */
    closeModalAlert(): void {
        const serverlist: string = Util.isUndefined(TpUtil.getCookie('tinyVoc')) ? '' : TpUtil.getCookie('tinyVoc');
        TpUtil.setCookie('tinyVoc', `${serverlist}${this.options.serviceId}${this.useId},`);
        this.showFeedBack = false;
    }

    /**
     * @ignore
     * 
     */
    onSubmit(): void {
        this.halfmodalCom.hide(); // 
        this.openModalAlert = false; // 
        this.showFeedBack = false;
    }
}
