import { Injectable, SecurityContext } from '@angular/core';
import { DomSanitizer } from '@angular/platform-browser';
import { TpGoandbackModule } from './TpGoandbackModule';
import { TpGoandbackComponent } from './TpGoandbackComponent';
import { TiMessageService, TiMessageType, TiModalRef, Util } from '@cloud/tiny3';
import { TpUtil } from '../../utils/TpUtil';

export interface TpGoAndBackConfig {
    /**
     * ||
     *
     * v10.1.3XSS(url)v10.1.4 
     */
    url?: string;
    /**
     * key:value
     */
    params?: { [propName: string]: string };
    /**
     * @ignore
     * 
     */
    words?: any;
    /**
     * @ignore
     * urlparamsrestrictgoToUrlurl
     * 10.1.2 
     */
    addUrlParams?: boolean;
    /**
     * ||instances,openAlert
     *
     * 1.instances:instances.dismiss()<br>
     * 2.openAlert(alertType):alertType
     *
     * a."error"b."warn"||
     */
    confirmFn?(instances?: TiModalRef, openAlertFn?: Function): void;
    /**
     * instances
     * 
     * 10.1.4 
     */
    closeFn?(instances?: TiModalRef): void;
    /**
     * 
     */
    [propName: string]: any;
}

/**
 * 
 */
@Injectable({
    providedIn: TpGoandbackModule
})
/**
 * @internal
 */
export class TpGoandbackService {
    /**
     * 
     */
    protected words: any;
    /**
     * 
     */
    protected firstMessageType: TiMessageType = 'warn';
    constructor(protected tiMessage: TiMessageService, private domSanitizer: DomSanitizer) {
    }

    public open(config?: TpGoAndBackConfig): void {
        // 
        this.words = config.words ? config.words : this.words;
        this.firstMessage(config);
    }
    /**
     * ||
     * @param configObj 
     */
    private firstMessage(configObj: TpGoAndBackConfig): void {
        const configId = Util.isString(configObj.id) && configObj.id !== '' ? configObj.id : 'tp_message_modal'; // idid
        const contentHref: string = this.words.firstMessageContentLabel ?
    `<a tiNavigation target="_blank" rel="noopener noreferrer" href="${this.words.firstMessageContentHref}">${this.words.firstMessageContentLabel}</a>` : '';
        const instances: TiModalRef = this.tiMessage.open({
            id: configId,
            type: this.firstMessageType,
            title: this.words.firstMessageTitle,
            content: Util.formatEntry(this.words.firstMessageContent, [contentHref]),
            okButton: {
                text: this.words.firstMessageOkButtonText,
                autofocus: true,
                click: (): void => {
                    const handleUrl: string = this.goToUrl(configObj);
                    instances.close();
                    // 
                    if (this.words.secondMessageTitle) {
                        this.secondMessage(configObj, handleUrl, configId);
                    }
                }
            },
            cancelButton: {
                show: false
            },
            close: (messageRef: TiModalRef): void => {
                // 
                if (!this.words.secondMessageTitle && configObj.closeFn && Util.isFunction(configObj.closeFn)) {
                    configObj.closeFn(messageRef);
                }
            }
        });
    }

    /**
     * ||
     * @param configObj 
     * @param handleUrl 
     */
    private secondMessage(configObj: TpGoAndBackConfig, handleUrl: string, promptId: string): void {
        const instances: TiModalRef = this.tiMessage.open({
            id: promptId + '_prompt',
            type: 'prompt',
            title: this.words.secondMessageTitle,
            modalClass: 'tp-goandback-body',
            content: TpGoandbackComponent,
            // context@Input/
            context: {
                words: this.words,
                href: handleUrl,
                label: this.words.secondMessageFail,
                warn: {
                    type: 'warn',
                    open: false,
                    id: promptId + '_prompt_warn'
                },
                error: {
                    type: 'error',
                    open: false,
                    id: promptId + '_prompt_error'
                }
            },
            okButton: {
                text: this.words.secondMessageOkButtonText,
                autofocus: true,
                click: (): void => {
                    if (configObj.confirmFn && Util.isFunction(configObj.confirmFn)) {
                        // tslint:disable-next-line: no-use-before-declare
                        configObj.confirmFn(instances, openAlertFn);
                    }
                }
            },
            close: (messageRef: TiModalRef): void => {
                // 
                if (configObj.closeFn && Util.isFunction(configObj.closeFn)) {
                    configObj.closeFn(messageRef);
                }
            }
        });

        /**
         * 
         * @param alertType (error: ) || (warn: ||)
         */
        const openAlertFn: (alertType: string) => void = (alertType: string): void => {
            const context: any = instances.content.instance.context;
            context.warn.open = alertType === 'warn' ? true : false;
            context.error.open = alertType === 'error' ? true : false;
        };
    }

    /**
     * @param definedUrl 
     * @param params 
     */
    private goToUrl(configObj: TpGoAndBackConfig): string {
        // addUrlParamstrue
        configObj.addUrlParams = Util.isUndefined(configObj.addUrlParams)
                                 ? true : configObj.addUrlParams;
        const [definedUrl, params] = [configObj.url, configObj.params];
        let url: string = Util.isString(definedUrl) ? definedUrl : this.words.path;
        // urlparamsrestrict issues-#1830
        if (configObj.addUrlParams) {
            url = TpUtil.addDefaultParam(url, params);
        }
        const win: any = window.open(this.domSanitizer.sanitize(SecurityContext.URL, url), '_blank');

        // IEwindow.open()
        if (win) {
            // 
            win.opener = null;
        }

        return url;
    }
}
