import { Component, ContentChild, Input, TemplateRef } from '@angular/core';
import { TiBaseComponent } from '@cloud/tiny3';

/**
 * @ignore
 * 10.1.3
 * 
 *
 * <example-url>../tinyplus3demo/#/stepguide/stepguide-all</example-url>
 */
@Component({
    selector: 'tp-stepguide',
    templateUrl: './stepguide.html',
    styleUrls: ['./stepguide.less']
})
export class TpStepguideComponent extends TiBaseComponent {
    /**
     * 
     *
     * 31.label2.iconClass/3.description
     */
    @Input() steps: Array<{
        label: string;
        iconClass: string;
        description: string;
    }>;
    /**
     * '156px'
     */
    @Input() stepMaxWidth: string;
    /**
     * @ignore
     * description
     */
    @ContentChild('description', { static: true }) description: TemplateRef<any>;
}
