import { Component, Input, SimpleChange, SimpleChanges, ViewEncapsulation } from '@angular/core';
import { TiBaseComponent } from '@cloud/tiny3';

// TODODOM
/**
 * @ignore IconIcon
 *
 *  IconIcon
 *
 * <example-url>../tinyplus3demo/#/icon/icon-all</example-url>
 */
@Component({
    selector: 'tp-icon[local]',
    template: '<ng-content></ng-content>',
    styleUrls: ['./iconlocal.less'],
    encapsulation: ViewEncapsulation.None // 
})
export class TpIconlocalComponent extends TiBaseComponent {
    /**
     * icon
     */
    @Input() name: string;

    ngOnChanges(changes: SimpleChanges): void {
        super.ngOnChanges(changes);
        const nameChange: SimpleChange = changes['name'];
        if (nameChange) {
            this.renderer.removeClass(this.nativeElement, `tp-icon-${nameChange.previousValue}`);
            this.renderer.addClass(this.nativeElement, `tp-icon-${nameChange.currentValue}`);
        }
    }
}
/* ti-icon
 */
