import {
   Component,
   EventEmitter,
   Input,
   Output,
   ViewChild
  } from '@angular/core';
import { Params } from '@angular/router';

import { TiBaseComponent, TiSelectComponent, Util, TiPositionType } from '@cloud/tiny3';

/**
 * itemsitem
 */
export interface TpSubtitleItem {
    label: string;
    [propName: string]: any;
}

@Component({
    selector: 'tp-subtitle',
    templateUrl: './subtitle.html',
    styleUrls: ['./subtitle.less'],
    host: {
        '[style.max-width]': 'maxWidth'
    }
})

/**
 * 
 *
 * <example-url>../tinyplus3demo/#/subtitle/subtitle-all</example-url>
 */
export class TpSubtitleComponent extends TiBaseComponent {
    /**
     * 
     */
    @Input() href: string = '';
    /**
     * 1
     */
    @Input() items: Array<TpSubtitleItem> = [];
    /**
     * items 1
     */
    @Input() searchable: boolean = false;
    /**
     * , atarget
     */
    @Input() target: '_blank' | '_self' | '_parent' | '_top' = '_blank';
    // select#selected, 
    /**
     * items 1
     */
    @Input('selected') selectedItem: TpSubtitleItem;
    /**
     * , hreftarget
     * 10.1.2 
     */
    @Input() backRouterLink: string | Array<any>;
    /**
     * 
     * 10.1.0 
     */
    @Input() queryParams: Params;
    /**
     * 
     * 10.1.2 
     */
    @Input() panelWidth: string;
    /**
     * 
     *  10.1.2 
     */
    @Input() maxWidth: string = '250px';
    /**
     * 
     *  10.1.3 
     */
    @Input() tipPosition: TiPositionType = 'top';
    /**
     * items 1
     */
    @Output() readonly selectedChange: EventEmitter<TpSubtitleItem> = new EventEmitter();
    /**
     * 
     */
    @Output() readonly back: EventEmitter<Event> = new EventEmitter();

    /**
     * @ignore
     */
    @ViewChild(TiSelectComponent) selectComRef: TiSelectComponent;
    ngOnInit(): void {
        super.ngOnInit();
        // items 1 
        if (Util.isUndefined(this.selectedItem) && this.items.length > 1) {
            this.selectedItem = this.items[0];
        }
    }

    ngAfterViewInit(): void {
      super.ngAfterViewInit();
      // 2 select
      if (this.items.length < 2) {
          return ;
      }
      // ti-select dark
      const attrs: NamedNodeMap = this.hostRef.nativeElement.attributes;
      if (!Util.isUndefined(attrs['dark'])) {
          this.renderer.setAttribute(this.selectComRef.nativeElement, 'dark', '');
      }
    }
    /**
     * @ignore
     * back 
     */
    public onClick($event: Event): void {
      this.back.emit($event);
    }
    /**
     * @ignore
     * selectsubtitle selectedChange
     */
    public onSelectedOptionChange(option: any): void {
       this.selectedChange.emit(option as TpSubtitleItem);
    }

}
