import { Component, ContentChild, ElementRef, EventEmitter, Input,  NgZone,  Output, Renderer2, TemplateRef } from '@angular/core';
import { FormGroup, ValidationErrors } from '@angular/forms';
import { TiBaseComponent, TiLocale, TiPopUpRef, TiPopupService, TiPosition, TiPositionType, TiValidators } from '@cloud/tiny3';

import { TpRenameModalContainerComponent } from './TpRenameModalContainerComponent';
import { TpRenameModalComponent } from './TpRenameModalComponent';

/**
 * renamehide()
 */
export interface TpRenameModalRef {
    /**
     * 
     */
    hide(): void;
}
/**
 * 
 *
 * <example-url>../tinyplus3demo/#/rename/rename-all</example-url>
 */
@Component({
    selector: 'tp-rename',
    templateUrl: './rename.html',
    styleUrls: ['./rename.less']
})

export class TpRenameComponent extends TiBaseComponent {
    constructor(
        protected hostRef: ElementRef,
        protected renderer2: Renderer2,
        private tiPopup: TiPopupService<any>,
        private zone: NgZone
    ) {
        super(hostRef, renderer2);
        this.defaultModalHeader = TiLocale.getLocaleWords().tpRename.modalHeader;
    }
    /**
     * 
     */
    @Input() value: string;
    /**
     * new
     */
    @Input() isNew: boolean = false;
    /**
     * 
     */
    @Input() disabled: boolean = false;
    /**
     * 
     */
    @Input() modalHeader: string;
    /**
     * @ignore
     * tip
     *
     * 4.0titletip10.0.1/Edit
     */
    @Input() iconTip: string;
    /**
     * @ignore
     * tip10.0.1
     */
    @Input() iconTipPosition: TiPositionType = 'top';
    /**
     * 10.0.1
     *
     * title
     */
    @Input() iconTitle: string = TiLocale.getLocaleWords().tpRename.tip;
    /**
     * 10.0.3
     *
     * 
     */
    @Input() showValue: boolean = true;
    /**
     * /OK
     */
    @Output() readonly confirm: EventEmitter<any> = new EventEmitter<any>();
    /**
     * /OK
     *
     * [TpRenameModalRef]{@link ../interfaces/TpRenameModalRef.html}
     */
    @Output() readonly beforeClose: EventEmitter<any> = new EventEmitter<any>();
    /**
     * @ignore
     * 
     */
    @ContentChild('renameModal', { static: true }) customModal: TemplateRef<any>;

    private hostSpace: number = 5; // rename

    private defaultModalHeader: string;

    private unlistenDocumentClick: () => void;

    private renameModal: TiPopUpRef;

    private TpRenameModalComponentRef: any;

    ngOnInit(): void {
        super.ngOnInit();
        this.iconTitle = this.iconTip ? '' : this.iconTitle;
    }

    /**
     * @ignore
     */
    public onClick(): void {
        if (this.disabled) {
            return;
        }
        // createpopupService
        if (!this.renameModal) {
            this.renameModal = this.tiPopup.create(TpRenameModalContainerComponent);
        }
        // open modal
        this.TpRenameModalComponentRef =  this.renameModal.show({
            content: this.customModal || TpRenameModalComponent,
            contentContext: this.customModal ?
                {
                    renameModal: this.renameModal,
                    value: this.value
                } : {
                value: this.value,
                id: this.id + '_renameModal',
                modalHeader: this.modalHeader || this.defaultModalHeader,
                close: (form: FormGroup): void => {
                    //  errorerrorsinput
                    const error: ValidationErrors | null = TiValidators.check(form);
                    if (error) {
                        this.TpRenameModalComponentRef.location.nativeElement.querySelector('input')
                            .focus();
                    } else {
                        this.beforeClose.emit(this.renameModal);
                        this.confirm.emit(this.TpRenameModalComponentRef.location.nativeElement.querySelector('input').value);
                        // tslint:disable-next-line: no-unused-expression
                        this.destroyRenameModal();
                    }
                },
                dismiss: (): void => {
                    this.destroyRenameModal();
                }
            },
            container: 'body'
        });
        const modal: ElementRef = this.TpRenameModalComponentRef.location.nativeElement;
        this.renderer2.setStyle(modal, 'left', '-9999px');
        this.renderer2.setStyle(modal, 'top', '-9999px');

        const td: ElementRef = this.findClosestTD(this.hostRef.nativeElement);
        TiPosition.setPosition({
            targetEle: modal,
            hostEle: this.hostRef.nativeElement,
            hostEleX: td || this.hostRef.nativeElement,
            hOffset: td ? 10 : 0,
            hostSpace: this.hostSpace,
            fixMaxHeight: true,
            determinPositionFn: this.DeterminePosition
        });

        this.addDocumentClickEvent();
    }

    private DeterminePosition: (layout: any) => string = (layout: any) => {
        if (layout.avilableLayout.bottom >= layout.targetLayout.height + this.hostSpace) {
            return 'bottom-left';
        } else {
            return 'top-left';
        }
    }

    private findClosestTD: (ele: any) => ElementRef = (ele: any) => {
        if (ele.nodeName === 'HTML') {
            return undefined;
        } else if (ele.parentNode.nodeName === 'TD') {
            return ele.parentNode;
        } else {
            return this.findClosestTD(ele.parentNode);
        }
    }

    private addDocumentClickEvent(): void {
        this.zone.runOutsideAngular((): void => {
            this.unlistenDocumentClick = this.renderer2.listen(document, 'click', (event: MouseEvent): void => {
                // click
                if (this.hostRef.nativeElement.contains(event.target)) {
                    return;
                }
                if (this.TpRenameModalComponentRef) {
                    const tpRenameModalHostElement: any = this.TpRenameModalComponentRef.location.nativeElement;
                    // renameModalrenameModal
                    if (!(tpRenameModalHostElement && tpRenameModalHostElement.contains(event.target))) {
                        this.zone.run((): void => {
                            this.destroyRenameModal();
                        });
                    }
                }

            });
        });
    }

    // documentclick
    private destroyRenameModal(): void {
        // tslint:disable-next-line: no-unused-expression
        this.renameModal && this.renameModal.hide();
        if (this.unlistenDocumentClick) {
            this.unlistenDocumentClick();
        }
    }

    // tslint:disable-next-line: use-life-cycle-interface
    ngOnDestroy(): void {
        super.ngOnDestroy();
        this.destroyRenameModal();
    }
}
