import { Pipe, PipeTransform } from '@angular/core';
import { TiLocale } from '@cloud/tiny3';
import { TpLocaleWords } from '../../locale/i18n';
import { formatDate } from '@angular/common';

/**
 * 
 *
 * 
 *
 * <example-url>../tinyplus3demo/#/locale/locale-all</example-url>
 */
@Pipe({ name: 'tpNumber', pure: false })
export class TpNumberPipe implements PipeTransform {

  /**
   * 
   * @param value 
   * @param format 1.2-4124
   * @returns 
   */
  transform(value: number, format?: string): string {
    // 
    let myoptions: any = this.getOptions();

    // 
    if (format) {
      myoptions = {
        ...myoptions,
        ...{
          minimumIntegerDigits: format.substring(0, format.indexOf('.')),
          minimumFractionDigits: format.substring(format.indexOf('.') + 1, format.indexOf('-')),
          maximumFractionDigits: format.substring(format.indexOf('-') + 1)
        }
      };
    }

    const res: string = new Intl.NumberFormat((TiLocale.getLocaleWords() as TpLocaleWords).tiLocaleKey, myoptions)
    .format(value);

    return res;
  }
  /**
   * 
   * @returns options
   */
  protected getOptions(): any {
    return (TiLocale.getLocaleWords() as TpLocaleWords).tpLocaleNumber;
  }
}
