import { Component, Input } from '@angular/core';
import { TiBaseComponent } from '@cloud/tiny3';

/**
 * 
 *
 * <example-url>../tinyplus3demo/#/beta/beta-all</example-url>
 */
@Component({
    selector: 'tp-beta',
    templateUrl: './beta.html',
    styleUrls: ['./beta.less']
})
export class TpBetaComponent extends TiBaseComponent {
    /**
     * 
     */
    @Input() icon: string;
    /**
     * 
     */
    @Input() moreUrl: string = '';
    /**
     * 
     */
    @Input() intro: string = '';
    /**
     * 
     */
    @Input() message: string = '';
    /**
     * 
     */
    @Input() verified: boolean = true;
    /**
     * 
     */
    @Input() applyUrl: string = '';
    /**
     * @ignore
     */
    public onClickFn(event: Event): void {
        if(!this.verified) {
            event.preventDefault();
        }
    }
}
