import { Component, ContentChild, EventEmitter, Input, Output, TemplateRef } from '@angular/core';

import { TiBaseComponent, TiLocale, TiPositionType } from '@cloud/tiny3';

export interface TpRecommendItem {
    /**
     * 
     */
    displayName: string;
    /**
     * 
     */
    num?: number;
    /**
     * tip
     */
    tip?: string | TemplateRef<any>;
    /**
     * tip
     */
    tipPosition?: TiPositionType;
    /**
     * 
     */
    showNum?: boolean;
    /**
     * 
     */
    [propName: string]: any;
}

/**
 * 
 *
 * <example-url>../tinyplus3demo/#/recommend/recommend-all</example-url>
 */
@Component({
    selector: 'tp-recommend',
    templateUrl: './recommend.html',
    styleUrls: ['./recommend.less'],
    // host: {
    //     '[class.tp-recommend-wrapper]': 'true'
    // }
})

export class TpRecommendComponent extends TiBaseComponent {
    /**
     * 
     */
    @Input() items: Array<TpRecommendItem>;
    /**
     * 
     */
    @Input() label: string;
    /**
     * 
     */
    @Output() readonly select: EventEmitter<any> = new EventEmitter<any>();
    /**
     * @ignore
     * itemDOM
     */
    @ContentChild('beforeItem', { static: true }) beforeItemTemplate: TemplateRef<any>;

    /**
     * @ignore 
     */
    public getNum(num: number): string {
        return num > 99 ? '99+' : String(num);
    }
    /**
     * @ignore
     */
    public clickFn(item: any): void {
        this.select.emit(item);
    }

    ngOnInit(): void {
        this.label = this.label || TiLocale.getLocaleWords().tpRecommend.label;
    }
}
