import { Component, EventEmitter, Input, Output, SimpleChanges } from '@angular/core';
import { TiBaseComponent, Util } from '@cloud/tiny3';
import { TpDiscoutsConfig } from '../discounts/TpDiscountsModule';

export interface TpHelpOptions {
    /**
     * 
     */
    iconTip: string;
    /**
     * 
     */
    iconTipMaxWidth?: string;
    /**
     * 
     */
    iconTipPosition?: string;
}

/**
 * 
 * <example-url>../tinyplus3demo/#/purchaselist/purchaselist-all</example-url>
 */
@Component({
    selector: 'tp-purchaselist',
    templateUrl: './purchaselist.html',
    styleUrls: ['./purchaselist.less']
})

export class TpPurchaselistComponent extends TiBaseComponent {
    /**
     * 
     */
    @Input() purchaseTitle: string;
    /**
     * 
     */
    @Input() enableClear: boolean = true;
    /**
     * 
     */
    @Input() totalPrice: number = 0;
    /**
     * 
     */
    @Input() discountPrice: number = 0;
    /**
     * 
     * 1.2-4124
     * 
     */
    @Input() numFormat: string;
    /**
     * discounts
     */
    @Input() purchaseDiscoutsConfig: TpDiscoutsConfig;
    /**
     * @ignore
     * 
     */
    @Input() hasNodata: boolean;
    /**
     * tip
     */
    @Input() helpOptions: TpHelpOptions;
    /**
     * 
     */
    @Input() footerProtocol: string;
    /**
     * 
     */
    @Input() footerPrompt: string;
    /**
     * 
     */
    @Input() buttonText: string;
    /**
     * 
     *
     * 10.1.1
     */
    @Input() buttonDisabled: boolean = true;
    /**
     * 
     */
    @Input() total: number = 0;
    /**
     * 
     */
    @Output() readonly confirm: EventEmitter<any> = new EventEmitter();
    /**
     * 
     */
    @Output() readonly clear: EventEmitter<any> = new EventEmitter();
    /**
     * 
     *
     * 10.1.2 
     */
    @Output() readonly discountChange: EventEmitter<any> = new EventEmitter();
    /**
     * @ignore
     */
    public emptyText: string = '--';
    /**
     * @ignore
     * 
     */
    public checked: boolean = false;
    /**
     * @ignore
     */
    public discountsChange($event: any) {
        this.discountChange.emit($event);
    }
    onCheckboxNgModelChange(model: boolean) {
        // Todo tip
    }
    /**
     * @ignore
     * 
     */
    public clickFn(): void {
        this.confirm.emit();
    }
    /**
     * @ignore
     * 
     */
    public clearFn(): void {
        this.clear.emit();
    }
}
