import { Component, EventEmitter, Input, Output, QueryList, SimpleChanges, ViewChild, ViewChildren } from '@angular/core';
import { TiDominatorComponent, TiDropComponent, TiDroplistComponent, TiFormComponent, TiListComponent,
    TiPositionType, TiWholeComponent} from '@cloud/tiny3';

export interface TpCascaderItem {
    label?: string;
    disabled?: boolean;
    /**
     * 
     *
     * 1.label 2.disabled 3.tip 4.tipPositionright
     */
    children?: Array<{
        label?: string;
        disabled?: boolean;
        tip?: any;
        tipPosition?: TiPositionType;
        [propName: string]: any;
    }>;
    /**
     * 
     */
    [propName: string]: any;
}
/**
 *  
 *
 * <example-url>../tinyplus3demo/#/cascader/cascader-all</example-url>
 */
@Component({
    selector: 'tp-cascader',
    templateUrl: './cascader.html',
    styleUrls: ['./cascader.less'],
    providers: [TiFormComponent.getValueAccessor(TpCascaderComponent)],
    host: {
        '(blur)': 'onBlur()',
        '[class.tp-cascader-dominator]': 'true'
    }
})

export class TpCascaderComponent extends TiFormComponent {
  // TODO: add explicit constructor

  // TODO: add explicit constructor

    /**
     * 
     */
    @Input() items: Array<TpCascaderItem>;
    /**
     * 
     */
    @Input() clearable: boolean = false;
    /**
     * @ignore
     * 10.1.0
     */
    // @Input() searchable: boolean = false;
    /**
     * 
     */
    @Input() placeholder: string;
    /**
     * 
     */
    @Input() labelKey: string = 'label';
    /**
     * 10.0.4 
     *
     * 
     */
    @Input() panelWidth: string = '160px';
    /**
     * 10.0.4 
     *
     * 
     */
    @Input() panelHeight: string = '248px';
    /**
     * 10.0.4 
     *
     * 
     */
    @Input() onlySelectLeaf: boolean = true;
    /**
     * 10.0.4 
     *
     * 
     */
    @Input() showAllLevel: boolean = true;
    /**
     * 10.1.1
     * 
     */
    @Input() valueKey: string;

    /**
     * @ignore
     * 
     */
    @Output() readonly select: EventEmitter<any> = new EventEmitter<any>();
    /**
     * @ignore
     * 
     */
    @Output() readonly change: EventEmitter<any> = new EventEmitter<any>();
    /**
     * 
     */
    @Output() readonly clear: EventEmitter<any> = new EventEmitter<any>();

    /**
     * @ignore
     */
    @ViewChild(TiDominatorComponent, {static: true}) dominatorCom: TiDominatorComponent;
    /**
     * @ignore
     */
    @ViewChild(TiDropComponent) dropCom: TiDropComponent;
    /**
     * @ignore list
     */
    @ViewChildren('commonList') listComs: QueryList<TiListComponent>;
    /**
     * @ignore droplist
     */
    @ViewChild('wideDroplist') wideDroplist: TiDroplistComponent;
    /**
     * @ignore  
     */
    public separator: string = '/';
    /**
     * @ignore panelWidth
     */
    public panelWidthNum: number;
    /**
     * @ignore TiListComponentmodel
     */
    public listModel: Array<any> = [];
    /**
     * @ignore dominator
     */
    public dominatorModel: string;
    /**
     * @ignore
     * itemsArr[0]itemArr[1]itemArr[2]
     */
    public itemsArr: Array<Array<any>> = [];
    /**
     * @ignore 
     */
    public itemForWideDroplist: Array<any> = [];
    /**
     * @ignore
     */
    public dropshow: boolean;
    /**
     * @ignore
     */
    public listComsArr: Array<TiListComponent>;

    /**
     * modelWholemodel
     */
    private _modelWhole: Array<TpCascaderItem>;

    private valueFn: (item: any) => any = (item: any) => {
        return item[this.valueKey];
    }

    ngOnInit(): void {
        this.panelWidthNum = parseInt(this.panelWidth, 10);
    }
    ngOnChanges(change: SimpleChanges): void {
        if (change['items']) {
            this.itemsArr[0] = this.items;
        }
    }
    /**
     * @ignore
     */
    ngOnModelChange(): void {
        if (this.modelWhole && this.modelWhole.length > 0) {
            this.joinDominatorModel();
        }
    }
    /**
     * @ignore
     */
    public onClick(): void {
        if (this.disabled) {
            return;
        }
        if (this.dropCom.isShow || this.wideDroplist.isShow) {
            this.close();

            return;
        }
        this.dropshow = true;
        if (this.items.length === 0) {
            this.wideDroplist.show();
        } else {
            /**
             * tiScrolldroplist
             * list
             * list
             */
            this.listComsArr.forEach((list: TiListComponent, index: number): void => {
                if (index > 0) {
                    this.renderer.setStyle(list.nativeElement, 'display', 'none');
                }
            });
            this.dropCom.show();
            if (this.modelWhole && this.modelWhole.length > 0) {
                this.modelWhole.forEach((item: TpCascaderItem, i: number): void => {
                    this.listComsArr[i].model = item;
                    this.itemsArr[i + 1] = item.children;
                    this.showList(i);
                    // setTimeout
                    setTimeout((): void => {
                        this.listComsArr[i].scrollToSelected();
                    }, 0);
                });
            } else {
                this.listComsArr[0].model = undefined;
                this.showList(0);
            }
        }

        this.focus();
    }
    ngAfterViewInit(): void {
        this.listComsArr = this.listComs.toArray();
        this.setFocusableElems(this.dominatorCom.getFocusableElems());
        this.renderer.setStyle(this.dropCom.nativeElement, 'width', '');
    }
    /**
     * @ignore
     */
    public close(): void {
        this.dropshow = false;
        this.dropCom.hide();
        this.wideDroplist.hide();
    }
    /**
     * @ignore
     */
    public onBlur(): void {
        this.close();
    }

    /**
     * @ignore
     */
    public onSelect($event: TpCascaderItem, panelIndex: number): void {
        // 
        if ($event.children?.length > 0) {
            // 
            for (let i: number = panelIndex + 1; i < this.listComsArr.length - 1; i++) {
                this.renderer.setStyle(this.listComsArr[i + 1].nativeElement, 'display', 'none');
            }
            // 
            this.itemsArr[panelIndex + 1] = $event.children;
            this.showList(panelIndex + 1);
        } else {
            // 
            this.close();
        }
        this.listModel = this.listModel || [];
        this.listModel.splice(panelIndex);
        this.listModel.push($event);
        if (!this.onlySelectLeaf || !$event.children || $event.children.length === 0) {
            this.modelWhole = [...this.listModel];
            this.handleModel($event, panelIndex);
        }
    }
    /**
     * @ignore
     */
    public onClear(): void {
        this.model = [];
        this.clear.emit();
    }
    /**
     * @ignore
     */
    public onMouseleave(panelIndex: number): void {
        this.listComsArr[panelIndex].hoverOption = undefined;
    }

    /**
     * @ignore
     */
    public showList(panelIndex: number): void {
        this.renderer.setStyle(this.listComsArr[panelIndex].nativeElement, 'display', 'inline-block');
        this.renderer.setStyle(this.dropCom.nativeElement, 'width', this.panelWidthNum * (panelIndex + 1) + 'px');
        if (!this.listComsArr[panelIndex].model) {
            this.listComsArr[panelIndex].hoverOption = undefined;
        }
    }

    private handleModel($event: TpCascaderItem, panelIndex: number): void {
        // 
        this.listModel.splice(panelIndex);
        this.listModel[panelIndex] = $event;
        this.joinDominatorModel();
    }

    /**
     * modeldominatorModeldominator
     */
    private joinDominatorModel(): void {
        this.dominatorModel = '';
        if (this.showAllLevel) {
            this.modelWhole.forEach((item: any, index: number): void => {
                if (index === 0) {
                    this.dominatorModel += `${item[this.labelKey]}`;
                } else {
                    this.dominatorModel += `${this.separator}${item[this.labelKey]}`;
                }
            });
        } else {
            this.dominatorModel = this.modelWhole[this.model.length - 1][this.labelKey];
        }
    }

    writeValue(model): void {
        // super.writeValue(model);
        if (model && this.valueKey) {
            this._modelWhole = this.items.filter((item) => model.includes(this.valueFn(item)));
            if (this._modelWhole[0].children) {
                const nextModelWhole: TpCascaderItem = this._modelWhole[0].children.filter((item) => model.includes(this.valueFn(item)))[0];
                this._modelWhole.push(nextModelWhole);
                if (nextModelWhole.children) {
                    const nextModelWhole1: TpCascaderItem =
                        nextModelWhole.children.filter((item) => model.includes(this.valueFn(item)))[0];
                    nextModelWhole1 && this._modelWhole.push(nextModelWhole1);
                }
            }
        } else {
            this._modelWhole = model;
        }
        super.writeValue(model);
    }

    private get modelWhole(): any {
        return this._modelWhole;
    }

    /**
     * modelWholemodel
     */
    private set modelWhole(value: any) {
        this._modelWhole = value;
        if (this.valueKey && value !== undefined && value !== null) {
            // valueKeyvalue
            this.model = value.map((item) => this.valueFn(item));
        } else {
            this.model = value;
        }
    }
}
