import { TpDiscoutsConfig } from '../discounts/TpDiscountsModule';
import { AfterViewInit, Component, ContentChild, ElementRef, EventEmitter, Input, NgZone, OnDestroy, Output,
    Renderer2, TemplateRef, ViewChild } from '@angular/core';
import { TpButtons } from './TpBuylayerRightComponent';
import { TpCalculator, TpCosts } from './TpBuylayerLeftComponent';
import { TiBaseComponent, TiBrowser } from '@cloud/tiny3';

export interface TpBuylayerConfig {
    /**
     * 
     */
    buttons?: Array<TpButtons>;
    /**
     * 
     */
    costs?: Array<TpCosts>;
    /**
     * 
     */
    costExplain?: string;
    /**
     * 
     */
    showFreechargeDetail?: boolean;
    /**
     * tip(),discounts
     */
    majorDiscoutsConfig?: TpDiscoutsConfig;
    /**
     * tip(),discounts
     */
    minorDiscoutsConfig?: TpDiscoutsConfig;
    /**
     * 
     * 1.2-4124
     * 
     */
    numFormat?: string;
    /**
     * 
     */
    detailHref?: string;
    /**
     * tip
     * 10.1.4
     */
    detailTipMaxWidth?: string
    /**
     * 
     */
    calculator?: TpCalculator;
    /**
     * 
     */
    isRefund?: boolean;
    /**
     * @ignore
     * Tip(10.1.0)
     */
    showMinorTip?: boolean;
    /**
     * @ignore
     * Tip(10.1.0)
     */
    showMajorTip?: boolean;
    /**
     * @ignore
     * tip(10.1.0)
     */
    showMajorDiscount?: boolean;
    /**
     * @ignore
     * tip(10.1.0)
     */
    showMinorDiscount?: boolean;
}
/**
 * 
 *
 * <example-url>../tinyplus3demo/#/buylayer/buylayer-all</example-url>
 */
@Component({
    selector: 'tp-buylayer',
    templateUrl: './buylayer.html',
    styleUrls: ['./buylayer.less'],
    host: {
        '[class.tp-buylayer-container]': 'true'
    }
})

export class TpBuylayerComponent extends TiBaseComponent implements AfterViewInit, OnDestroy {
    constructor(private element: ElementRef, private renderer2: Renderer2, private ngZone: NgZone) {
        super(element, renderer2);
    }
    @Input() buylayerConfig: TpBuylayerConfig;
    /**
     * 
     *
     * 9.0.3 
     */
    @Output() readonly discountsChange: EventEmitter<any> = new EventEmitter();
    @ContentChild('leftZone') leftZoneEle: TemplateRef<any>;
    /**
     * @ignore
     */
    @ViewChild('buylayerContent', {static: false}) buylayerContentEle: ElementRef;
    private unlistenWindowScroll: () => void;
    // tslint:disable-next-line:use-life-cycle-interface
    ngAfterViewInit(): void {
        // ie
        if (TiBrowser.isIE()) {
            const contentContainerHeight: number = this.buylayerContentEle.nativeElement.offsetHeight;
            if (contentContainerHeight <= 70) { // 70px
                this.renderer2.setStyle(this.buylayerContentEle.nativeElement, 'height', 70 + 'px');
            }
        }

        this.ngZone.runOutsideAngular((): void => {
            // fixed
            this.unlistenWindowScroll = this.renderer2.listen('window', 'scroll', (): void => {
                this.renderer2.setStyle(this.element.nativeElement, 'left', `-${document.documentElement.scrollLeft || document.body.scrollLeft}px`);
            });
        });
    }

    ngOnDestroy(): void {
        if (this.unlistenWindowScroll) {
            this.unlistenWindowScroll();
        }
    }
    /**
     * @ignore
     */
    public change($event: Event): void {
        this.discountsChange.emit($event);
    }
}
