import { ChangeDetectorRef, Component, ElementRef, Input, Renderer2, SimpleChange, SimpleChanges, ViewChild } from '@angular/core';
import { TiBaseComponent, TiLocale, Util } from '@cloud/tiny3';
import { Subscription } from 'rxjs';

/**
 * console
 *
 * 10.1.0/9.1.0
 */
export interface TpRegionData {
    /**
     * region
     */
    region?: any;
    /**
     * region
     */
    supportRegions?: Array<any>;
    /**
     * 
     */
    [propName: string]: any;
}

/**
 * 
 *
 * console
 * servicecmregionregionData
 *
 * <b></b>region
 *
 * <example-url>../tinyplus3demo/#/nonsupportregion/nonsupportregion-all</example-url>
 */
@Component({
    selector: 'tp-nonsupportregion',
    templateUrl: './nonsupportregion.html',
    styleUrls: ['./nonsupportregion.less']
})

export class TpNonsupportregionComponent extends TiBaseComponent {
    /**
     * consolegetRegionData()regionsupportRegions
     *
     * consolegetRegionData()
     *
     * 10.1.0/9.1.0
     */
    @Input() regionData: TpRegionData;
    /**
     * @ignore
     */
    @ViewChild('container') containerEle: ElementRef;
    /**
     * @ignore
     */
    label: string;
    /**
     * @ignore
     */
    supportRegions: Array<any>;
    private regionsDataSub: Subscription;

    constructor(
        private elementRef: ElementRef,
        private renderer2: Renderer2,
        private changeDetector: ChangeDetectorRef) {
        super(elementRef, renderer2);
    }

    ngOnInit(): void {
        super.ngOnInit();
        if (this.regionData) {
            this.setViewInfo(this.regionData);

            return;
        }

        this.setRegionsData();
    }

    ngOnChanges(changes: SimpleChanges): void {
        super.ngOnChanges(changes);
        const regionsObj: SimpleChange = changes['regionData'];
        if (regionsObj && regionsObj.currentValue && !regionsObj.isFirstChange()) {
            this.setViewInfo(regionsObj.currentValue);
        }
    }

    ngOnDestroy(): void {
        super.ngOnDestroy();
        if (this.regionsDataSub) {
            this.regionsDataSub.unsubscribe();
        }
    }

    private setRegionsData(): void {
        if (!(window as any).cfRegionSubject) {
            return;
        }
        this.regionsDataSub = (window as any).cfRegionSubject.subscribe({
            next: (items: TpRegionData): void => {
                this.setViewInfo(items);
            }
        });
    }

    private setViewInfo(items: any): void {
        if (items.region && items.region.displayName) {
            const displayName: any = `<span>${items.region.displayName}</span>`;
            this.label = Util.formatEntry(TiLocale.getLocaleWords().tpNonsupportregion.label, [displayName]);
        }
        this.supportRegions = items.supportRegions;
        this.changeDetector.detectChanges();
    }
}
