import { Pipe, PipeTransform } from '@angular/core';
import { TiLocale } from '@cloud/tiny3';

import { formatDate } from '@angular/common';
import { TpNumberPipe } from './TpNumberPipe';
import { TpLocaleWords } from '../../locale/i18n';
/**
 * 
 *
 * 
 *
 * <example-url>../tinyplus3demo/#/locale/locale-all</example-url>
 */
@Pipe({ name: 'tpCurrency', pure: false })
export class TpCurrencyPipe extends TpNumberPipe implements PipeTransform {
/**
 * 
 * @param value 
 * @param format 1.2-4124
 * @param type 'short'USD
 * @returns 
 */
transform(value: number, format?: string, type?: 'short'|undefined): string {
    // 
    let res: string = super.transform(value, format);
    // 
    res = res.replace('CN', '');
    res = res.replace('US$', '$');
    res = res.replace('$', '$');
    if (this.getOptions().currency === 'USD' && type !== 'short') {
      res = res + ' USD';
    }
    if (res.includes('') && !res.endsWith('')) {
      res = res.replace('', '');
      res = res + ' ';
    }

    return res;
  }
  /**
   * 
   * @returns options
   */
  protected getOptions(): any {
    return (TiLocale.getLocaleWords() as TpLocaleWords).tpLocaleCurrency;
  }

}
