
import { Component, EventEmitter, Input, Output } from '@angular/core';
import { TiBaseComponent } from '@cloud/tiny3';
/**
 *  collapsebox
 *
 * collapsebox
 *
 * <example-url>../tinyplus3demo/#/collapsebox/collapsebox-all</example-url>
 */
@Component({
    selector: 'tp-collapsebox',
    templateUrl: './collapsebox.html',
    styleUrls: ['./collapsebox.less']
})

export class TpCollapseboxComponent extends TiBaseComponent {
    /**
     * @ignore
     * 
     */
    public collapsed: boolean = false;
    /**
     * 
     * 10.0.3
     */
    @Input() closeable: boolean = true;
    /**
     * 
     * 10.0.3
     */
    @Output() readonly close: EventEmitter<any> = new EventEmitter<any>();
    /**
     * @ignore
     * 10.0.3
     */
    public onClickClose(event: Event): void {
        if (this.close.observers.length > 0) {
            this.close.emit();
        } else {
            this.collapsed = true;
        }
    }
 }
