import { Component, EventEmitter, Input, Output } from '@angular/core';
import { TiBaseComponent } from '@cloud/tiny3';
import { TpLink } from '../crumb/TpCrumbModule';

export interface TpLinkOption {
    /* tslint:disable:no-redundant-jsdoc */
    /**
     * ;
     */
    title?: string;
    /**
     * ;
     * @default false
     */
    isNavigation?: boolean;
    /**
     * ;
     * @default false
     */
    hide?: boolean;
    /**
     * ;
     */
    items?: Array<TpLinkItem>;
    /**
     * 
     */
    [propName: string]: any;
}
export interface TpLinkItem extends TpLink {
    /**
     * ;
     * @default false
     */
    hide?: boolean;
}
/**
 *  linklist
 *
 * <example-url>../tinyplus3demo/#/linklist/linklist-all</example-url>
 */
@Component({
    selector: 'tp-linklist',
    templateUrl: './linklist.html',
    styleUrls: ['./linklist.less']
})

export class TpLinklistComponent extends TiBaseComponent {
    /**
     * 
     */
    @Input() options: Array<TpLinkOption>;
    /**
     * 
     */
    @Output() readonly select: EventEmitter<TpLinkItem> = new EventEmitter<TpLinkItem>();

    /**
     * @ignore
     */
    public onClick(item: TpLinkItem): boolean {
        this.select.emit(item);
        // aclickfalse
        if (!item.href) {
            return false;
        }
    }
}
